/* General styles */
body {
  margin: 0;
  font-family: Arial, sans-serif;
}

main {
  width: 100%;
  overflow-x: hidden;
}

/* Video Section Styles */
.video-section {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  text-align: center;
}

.background-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.overlay {
  z-index: 1;
  padding: 0 20px;
}

.overlay h1 {
  font-size: 5rem;
  margin: 0;
  font-weight: bold;
  padding-top: 75px;
}

.overlay p {
  font-size: 2.2rem;
  margin-top: 1rem;
  font-weight: bold;
  color: #622830;
  padding-bottom: 150px;
}

@media (max-width: 768px) {
  .overlay h1 {
    padding-top: 60px;
    font-size: 2rem;
  }
  .overlay p {
    font-size: 1rem;
    padding-bottom: 100px;
  }
}

@media (max-width: 600px) {
  .overlay h1 {
    padding-top: 50px;
    font-size: 1.8rem;
  }
  .overlay p {
    font-size: 1rem;
    padding-bottom: 50px;
  }
}

.overlay img {
  max-width: 30%;
  height: auto;
}

@media (max-width: 768px) {
  .overlay img {
    max-width: 40%;
  }
}

/* Who We Are Section */
.who-we-are-header {
  padding: 0px 80px;
  display: flex;
  align-items: flex-start;
}

.who-we-are-header h2 {
  font-size: 3rem;
  color: #622830;
  margin-bottom: 20px;
}

@media (max-width: 1100px) {
  .who-we-are-header {
    padding: 0px 20px;
  }
}

@media (max-width: 768px) {
  .who-we-are-header h2 {
    font-size: 2.2rem;
  }
}

.who-we-are-text {
  padding: 0px 0px;
  display: flex;
  align-items: flex-start;
}

.who-we-are-text p {
  font-size: 1.2rem !important;
  line-height: 1.8;
  color: #333;
}

/* Text container */
.who-we-are-text > div:first-child {
  flex: 1;
  padding-right: 0px;
  margin-left: 80px;
  padding-left: 20px;
  border-left: 3px solid #622830;
}

/* Image container */
.who-we-are-text > div:last-child {
  flex: 1;
  padding-top: 20px;
  padding-right: 40px;
  align-items: flex-start;
}

/* Responsive styling for mobile */
@media (max-width: 1100px) {
  .who-we-are-text {
    flex-direction: column;
    padding: 10px;
  }

  .who-we-are-text p {
    line-height: 1.4;
    font-size: 1rem !important;
  }

  .who-we-are-text > div:first-child {
    padding-left: 20px;
    margin-left: 20px;
    padding-right: 20px;
  }

  .who-we-are-text > div:last-child {
    padding-top: 20px;
    padding-right: 0px;
    align-self: center;
  }
}

/* Learn More Button */
.learn-more-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 20px;
  padding: 20px 20px;
}

.learn-more-button-details {
  background-color: #622830;
  color: #fff;
  padding: 10px 15px;
  font-size: 2.5rem;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  letter-spacing: 1px;
  cursor: pointer;
  text-decoration: none;
  text-align: center;
}

@media (max-width: 768px) {
  .learn-more-button-details {
    font-size: 1.8rem;
  }
}

/* Background Carousel */
.background-carousel-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  text-align: center;
  z-index: 2;
}

.background-carousel-container {
  position: relative;
  width: 100%;
  overflow: hidden;
  height: 100vh;
}

.background-carousel-overlay h1 {
  font-size: 2.5rem;
  font-weight: bold;
  color: #622830;
}

.background-carousel-overlay p {
  font-size: 5rem;
  font-weight: bold;
  margin-top: 0.2rem;
}

.background-carousel-button {
  display: inline-block;
  background-color: #622830;
  color: #fff;
  font-size: 3.5rem;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  letter-spacing: 1px;
  cursor: pointer;
  padding: 10px 15px;
  text-decoration: none;
}

@media (max-width: 768px) {
  .background-carousel-overlay h1 {
    font-size: 1.9rem;
  }

  .background-carousel-overlay p {
    font-size: 2.8rem;
  }

  .background-carousel-button {
    font-size: 1.5rem;
  }
}

/* Scroll Section Styles */
.content-section {
  background: white;
  padding: 50px 20px;
  text-align: center;
}

.content-section h2 {
  font-size: 2rem;
  margin-bottom: 20px;
}

.content-section p {
  font-size: 1rem;
  margin-bottom: 30px;
}

.content-section button {
  background: #ff6666;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
}

.content-section button:hover {
  background: #ff4d4d;
}
