/* Join Page Header */
.join-header {
  padding: 0px 120px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.join-header h1 {
  font-size: 3rem;
  color: #622830;
  letter-spacing: 1px;
  text-align: left;
}

.join-header h2 {
  font-size: 2.2rem;
  font-weight: 500;
  color: #622830;
  margin-top: 10px;
  text-align: left;
}

@media (max-width: 768px) {
  .join-header {
    padding: 0px 40px;
  }

  .join-header h1 {
    font-size: 2.5rem;
    text-align: center;
  }

  .join-header h2 {
    font-size: 1.7rem;
    text-align: center;
  }
}

/* Apply Section */
.join-text {
  padding: 0px 20px;
  display: flex;
  align-items: flex-start;
}

.join-text > div:first-child {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  padding: 15px 80px 0 20px;
}

.join-text > div:last-child {
  flex: 3;
  padding: 20px 40px 0 40px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.join-text p {
  font-size: 1.5rem;
}

.apply-button {
  background-color: #622830;
  color: #fff;
  padding: 15px 20px;
  font-size: 2.8rem;
  font-weight: normal;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
}

@media (max-width: 768px) {
  .join-text {
    flex-direction: column;
    padding: 10px;
  }

  .join-text > div:first-child {
    padding-left: 20px;
    margin-left: 20px;
    padding-right: 20px;
  }

  .join-text > div:last-child {
    padding-top: 20px;
    padding-right: 0px;
    padding-left: 0px;
    align-self: center;
  }

  .join-text p {
    text-align: center;
  }
}

/* Optional Timeline Image */
.recruitment-timeline {
  max-width: 80%;
  height: auto;
  margin: 20px;
}

@media (max-width: 768px) {
  .recruitment-timeline {
    max-width: 90%;
  }
}

/* Questions Section */
.questions {
  padding: 0px 120px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.questions h1 {
  font-size: 3rem;
  color: #622830;
  letter-spacing: 1px;
  text-align: left;
  font-weight: normal;
}

.questions h2 {
  font-size: 1.5rem;
  font-weight: 500;
  color: #622830;
  margin-bottom: 60px;
  text-align: left;
}

@media (max-width: 768px) {
  .questions {
    padding: 0px 40px;
  }

  .questions h1 {
    font-size: 1.7rem;
    text-align: center;
  }

  .questions h2 {
    font-size: 1.5rem;
    text-align: center;
  }
}
