.people-container {
  padding: 2rem 12px;
  text-align: center;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
}

.team-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
  padding: 0 120px;
  max-width: calc(100% - 240px);
  text-decoration: none;
}

.title {
  font-size: 3rem !important;
  color: #622830 !important;
  margin-bottom: 2rem;
}

.team-card {
  background: #8d404c;
  border-radius: 8px;
  padding: 1rem;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-decoration: none;
  max-width: 200px;
  max-height: 320px;
}

.team-card a {
  text-decoration: none;
  color: #fff;
}

.team-link {
  text-decoration: none;
  color: #fff;
}

.team-card:hover {
  transform: scale(1.05);
}

.team-image {
  width: 100%;
  padding-top: 5%;
  border-radius: 6px;
  margin-bottom: 1rem;
}

.team-name {
  font-size: 1.2rem;
  font-weight: normal;
  color: #fff;
  text-decoration: none;
  margin-bottom: 0;
}

.team-position {
  margin-top: 0;
  font-size: 1rem;
  color: #f8dbdf;
  text-decoration: none;
}

/* Media Queries for Mobile */
@media (max-width: 932px) {
  .title {
    font-size: 2rem !important;
  }

  .team-name {
    font-size: 1rem;
  }

  .team-position {
    font-size: 0.7rem;
  }

  .team-container {
    padding: 0 10px !important;
    gap: 10px;
    max-width: calc(175% - 240px);
  }

  .team-card {
    padding: 0.5rem !important;
    max-width: 125px;
    height: 250px;
  }

  .team-image {
    margin-bottom: 0;
  }
}
